import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import { TemplateButton } from '../models'

export default {
  name: TemplateButton.SUB_TYPE,

  match: (node: JsonML): boolean => {
    const tag = getTagName(node);
    const attrs = getAttributes(node) as Record<string, any>;
    return tag === TemplateButton.TYPE && attrs?.subType === TemplateButton.SUB_TYPE;
  },

  convert: (): React.ReactElement => {
    // 兜底显示空 div
    return <div>{null}</div>;
  },
};
